
.App{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #18191B;
    color: #BDBCB8; 
    overflow-x: hidden;
}
.navbar{
    width: 68%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.navbar .logo{
    display: flex;
    justify-content: center;
    align-items: center;
}
.navbar .fa-gear,.navbar h2{
    color: #4CD1FD;
    padding-left: 5px;
}
.navigation-links ul{
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
    gap: 20px;
}
.navigation-links ul li a{
    text-decoration: none;
    color: #8D8E90;
    transition: 0.5s;
}
.navigation-links ul li a:hover{
    color: white;
}
.faBars,.faXmark{
    color: #BDBCB8;
    display: none;
}
.mobile-navBtn{
    display: none;
}



.main-heading{
    width: 80%;
}
.main-heading h1{
    font-size: 40px;
    text-transform: capitalize;
    text-align: center;
}



.input-search{
    width: 73%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
.input-search > div{
    width: 100%;
}
input[type="search"]{
    width: 100%;
    background-color: transparent;
    outline: none;
    border: solid 2px #1F2324;
    padding: 10px;
    border-radius: 7px;
    color: #363739;
}


.input-check-form{
    width: 70%;
    display: grid;
    justify-content: center;
    align-items: center;
    grid-template-columns: auto auto auto auto auto auto;
    column-gap: 25px;
    row-gap: 12px;
    margin-top: 25px;
}
.input-check-form label,.input-check-form2 label{
    font-size: 13px;
}


.input-check-form2{
    width: 70%;
    display: grid;
    grid-template-columns: auto auto auto auto auto auto;
    justify-content: center;
    align-items: center;
    column-gap: 20px;
    margin-top: 35px;
}

.picks{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 25px;
}
.picks label{
    font-size: 18px;
    font-weight: bolder;
}


.image-div{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    margin-top: 20px;
}

.first-image{
    background-image: url(image1.jpg);
    background-size: cover;
    width: 250px;
    border-radius: 7px;
    height: 45px;
}
.second-image{
    background-image: url(image2.jpg);
    background-size: cover;
    width: 250px;
    border-radius: 7px;
    height: 45px;
}


.cards-container{
    width: 73%;
    margin-bottom: 20px;
}
.card-header{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.card-header label{
    font-size: 14px;
}

.select-div{
    background-color: #1E1F21;
    padding:5px 10px;
    border-radius: 7px;
    border: solid 2px #292A2C;
}
.select-div select{
    background-color: transparent;
    outline: none;
    border: none;
    color: #8D8E90;
}

.cards-div{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 20px;
}
.cards{
    display: grid;
    grid-template-columns: auto auto;
    column-gap: 24px;
    row-gap: 20px;
}
.card{
    display: flex;
    justify-content: center;
    align-items: center;
    border: solid 2px skyblue;
    border-radius: 7px;
    width: 48.5%;
    height: 160px;
    gap: 15px;
    width: 100%;
}
.card2{
    border: solid 2px #2E2F31;
}
.card-image{
    background-image: url(image1.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    width: 50%;
    border-radius: 7px;
    height: 100%;
}
.card-content{
    padding-right: 5px;
}
.card-content p{
    margin-bottom: 10px;
    font-size: 14px;
}
.card-content h4{
    margin-top: 10px;

    color: darkcyan;
}
.card-content a{
    text-decoration: none;
    color: #8D8E90;
    background-color: #363739;
    padding: 2px 15px;
    font-size: 13px;
    border-radius: 5px;
}


@media (max-width: 1208px){
    .cards-container{
        width: 85%;
    }
}

@media(max-width:1183px){
    .input-check-form{
        width: 80%;
        justify-content: end;
    }
}

@media (max-width:1133px){
    .main-heading h1{
        font-size: 30px;
    }
}

@media(max-width:1083px){
    .input-check-form{
        width: 90%;
        justify-content: center;
        column-gap: 10px;
    }
}
@media (max-width:1040px){
    .cards-container{
        width: 90%;
    }
}
@media (max-width:980px){
    .navbar {
        position: relative;
        width: 100%;
        justify-content: space-around;
    }
    .logo h2{
        font-size: 20px;
    }
    

    .navigation-links {
        width: 100%;
        height: 100vh;
        background-color: #18191B;
        position: absolute;
        top: 0;
        left: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        transform: translateX(100%);
        transition: 0.5s;
        opacity: 0;
        visibility: hidden;
    }

    .active .navigation-links {
        transform: translateX(0%);
        opacity: 1;
        visibility: visible;
    }

    .active .faXmark {
        display: block;
    }

    .active .faBars {
        display: none;
    }

    .navigation-links ul {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-left: 0px;
        gap: 50px;
    }

    .navigation-links ul li a {
        font-size: 20px;
    }

    .faBars {
        display: block;
        z-index: 999;
    }
    .mobile-navBtn{
        z-index: 999;
        display: block;
    }
    
    .cards-container{
        width: 80%;
    }
    .cards-div{
        flex-direction: column;
        gap: 20px;
    }
    .card,.card2{
        width: 550px;
    }
    .cards{
        grid-template-columns: auto;
        justify-content: center;
        align-items: center;
        width: 100%;
    }

}

@media(max-width:850px){
    .main-heading{
        width: 90%;
    }
    .input-check-form{
        grid-template-columns: auto auto auto auto auto;
    }
    .input-check-form label{
        font-size: 11px;
    }
}
@media(max-width:755px){
    .main-heading h1{
        font-size: 25px;
    }
    .input-check-form2{
        width: 90%;
    }
}
@media(max-width:688px){
   input[type="search"]::placeholder{
        font-size: 11px;
    }
  .card{
    width: 450px;
  }
    
}
@media(max-width:630px){
    .input-check-form{
        grid-template-columns: auto auto auto auto;
        justify-content: space-between;
    }
}
@media(max-width:582px){
    .input-search{
        width: 95%;
    }
    .input-check-form2{
        grid-template-columns: auto auto auto;
        justify-content: space-evenly;
    }
    .image-div{
        flex-direction: column;
    }
    .card-header h4{
        font-size: 15px;
    }
   
}
@media (max-width:542px){
    .card{
        width: 400px;
    }
}
@media(max-width:520px){
    .input-check-form{
        column-gap: 5px;
        row-gap: 7px;
        grid-template-columns: auto auto auto;
    }
    .cards-container{
        width: 90%;
    }
    .card-content p{
        font-size: 13px;
    }
    
}
@media (max-width:447px){
   
    .card-content p{
        font-size: 12px;
    }
    .card{
        width: 350px;
    }
    .card-content h4{
        font-size: 13px;
    }
}
@media(max-width:425px){
    .main-heading h1{
        font-size: 18px;
    }
    input[type="search"]::placeholder{
        font-size: 9px;
    }
}
@media(max-width:415px){
    .card-content{
        text-align: center;
    }
}
@media(max-width:407px){
    .card-image{
        min-width: 27%;
    }
    .card{
        width: 300px;
    }
}
@media(max-width:383px){
    .input-check-form label{
        font-size: 9px;
    }
}
@media(max-width:340px){
    .card{
        width: 280px;
    }
}
@media(max-width:327px){
    .input-check-form{
        grid-template-columns: auto auto;
        justify-content: space-around;
    }
    .input-check-form label{
        font-size: 11px;
    }
    .input-check-form2 label{
        font-size: 11px;
    }
}




  


  
  
  
  
  